@font-face {
    font-family: Din;
    src: url(/fonts/DIN\ Medium.ttf);
}

@font-face {
    font-family: light;
    src: url(/fonts/DIN.ttf);
}

body{
    margin: 0;
    padding: 0;
    color: black;
    background-color: #FEFAF6;
}

//darkmode 
.darkmode{
    background-color: #22092C;
    color: white;
}

.darkmode a{
    color: white;
}

//navbar css
.navbar img{
  width: 5%;
}

.navbar{
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    justify-content: space-around;
    font-family: Din;
    font-size: large;
}

.navbar nav{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 50%;
}

.navbar nav a{
    text-decoration: none;
    color: black;
}

.navbar .icons{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 6%;
}

//css for mainbody
.mainbody{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-evenly;
    align-items: center;
    font-size: x-large;
    font-family: light;
    font-weight: lighter;
}

.mainbody .mainbody_icons{
    display: flex;
    flex-direction: column;
    font-size: 50px;
    margin-top: -5%;
}

.mainbody .mainbody_icons .icon1{
    position: absolute;
  margin-top:4%;
  
}

.mainbody img{
    width: 35%;
}
